import React, { useState, useEffect } from 'react';
import Sidebar from "./components/Sidebar";
import FullScreenImage from './components/FullscreenImage'

import { initializeApp } from 'firebase/app';
import firebaseconfig from "../firebaseconfig";

import { getFirestore, collection, getDocs } from 'firebase/firestore/lite';
import './main.css'

import hr_image from '../img/artmaster-fire-skill_1.gif'
import hr_image2 from '../img/artmaster-fire-skill_2.gif'
import hr_image3 from '../img/artmaster-fire-skill_3.gif'

import image from '../img/downres/artmaster-fire-skill_1.gif'
import image2 from '../img/downres/artmaster-fire-skill_2.gif'
import image3 from '../img/downres/artmaster-fire-skill_3.gif'

const app = initializeApp(firebaseconfig);

// Get a list of cities from your database
async function getEffectCards() {
    const effects_card_collections = collection(getFirestore(app), 'effects');
    const effectCardsSnapshot = await getDocs(effects_card_collections);
    const effectCards = effectCardsSnapshot.docs.map(doc => doc.data());
    return effectCards;
}

interface Image {
    id: number;
    src: string;
    highres_src: string;
    alt: string;
    description: string,
    filters: string[];
}


interface GalleryProps {
    images: Image[];
    onImageClick: (image: Image) => void;
    resetSearch: () => void;
}

interface ImageProps {
    image: Image;
    onClick: (image: Image) => void;
}

const Gallery: React.FC<GalleryProps> = ({ images, onImageClick, resetSearch }) => {
    return (
        <div className="gallery"

             style={{
                 display: 'flex',
                 flexWrap: 'wrap',
                 justifyContent: 'flex-start',
                 alignItems: 'flex-start',
                 width: "85%",
             }}
        >

            {images.length > 0 ? (
                images.map((image) => (
                    <Image key={image.id} image={image} onClick={onImageClick} />
                ))
            ) : (
                <div className="no-results">
                    No results found.{' '}
                    <button type="button" className={"bg-red-500 hover:bg-red-700 text-white font-bold py-2 px-4 rounded"} onClick={resetSearch}>
                        Reset search
                    </button>
                </div>
            )}
        </div>
    );
};

const Image: React.FC<ImageProps> = ({ image, onClick }) => {
    const [isFullScreen, setIsFullScreen] = useState(false);

    const handleClick = () => {
        setIsFullScreen(!isFullScreen);
    };

    return (
        <div
            style={{
                position: 'relative',
            }}
        >
            <img
                src={image.src}
                alt=""
                style={{
                    maxWidth: '100%',
                    height: '400px',
                    margin: "5px",
                    cursor: 'zoom-in',
                    borderRadius: '20px',
                    // objectFit:'contain'
                }}
                onClick={handleClick}
            />
            {isFullScreen && (
                <FullScreenImage
                    isOpen={isFullScreen}
                    closeModal={() => setIsFullScreen(false)}
                    imageUrl={image.highres_src}
                    onClose={handleClick}
                />
            )}
            {image.description && (
                <p
                    style={{
                        position: 'absolute',
                        bottom: 0,
                        left: 0,
                        right: 0,
                        padding: "15px",
                        background: 'rgba(255, 255, 255, 0.2)',
                        textAlign: "center",
                        color: "#FFFFFF",
                        fontWeight: "700",
                        fontSize:"20px"
                    }}
                >
                    {image.description}
                </p>
            )}
        </div>
    );
};

const App: React.FC = () => {

    const [filteredImages, setFilteredImages] = useState<Image[]>([]);
    const [selectedFilters, setSelectedFilters] = useState<string[]>([]);
    const [selectedImage, setSelectedImage] = useState<Image | null>(null);
    const [allImages, setAllImages] = useState<Image[]>([]);

    const filters = [
        "summoners war","default skill","aoe skill","seal","fire","water","wind","brush","single target","enemies","aoe","range","allies","crit","multi-hit","buff"
    ];

    useEffect(() => {
        // Simulate a request to retrieve images from a server
        setTimeout(() => {
            setAllImages([
                {
                    id: 1,
                    src: image,
                    highres_src: hr_image,
                    alt: 'Image 1',
                    description: "first image",
                    filters: ['Default Skill', 'Fire', "Brush"]
                },
                {
                    id: 2,
                    src: image2,
                    highres_src: hr_image2,
                    alt: 'Image 2',
                    description: "second image",
                    filters: ['Fire', 'Buff', 'AoE']
                },
                {
                    id: 3,
                    src: image3,
                    highres_src: hr_image3,
                    alt: 'Image 3',
                    description: "third image",
                    filters: ['Seal', 'Fire']
                }
            ]);
        }, 1000);
    }, []);

    useEffect(() => {
        setFilteredImages(
            allImages.filter((image) => {
                return selectedFilters.every((filter) => image.filters.includes(filter));
            })
        );
    }, [selectedFilters, allImages]);

    const handleFilterChange = (filter: string) => {
        if (selectedFilters.includes(filter)) {
            setSelectedFilters(selectedFilters.filter((f) => f !== filter));
        } else {
            setSelectedFilters([...selectedFilters, filter]);
        }
    };

    const handleImageClick = (image: Image) => {
        setSelectedImage(image);
    };

    const handleCloseFullScreenImage = () => {
        setSelectedImage(null);
    };

    const handleResetSearch = () => {
        setSelectedFilters([]);
    };

    return (
        <div className="app">
            <Sidebar filters={filters} selectedFilters={selectedFilters} onFilterChange={handleFilterChange} resetSearch={handleResetSearch} />
            <Gallery images={filteredImages} onImageClick={handleImageClick} resetSearch={handleResetSearch} />
            {/*{selectedImage && <FullScreenImage image={selectedImage} onClose={handleCloseFullScreenImage} />}*/}
        </div>
    );
};

export default App;
