import React from "react";
import './sidebar.css'
interface SidebarProps {
    filters: string[];
    selectedFilters: string[];
    onFilterChange: (filter: string) => void;
    resetSearch: () => void;
}

const Sidebar: React.FC<SidebarProps> = ({ filters, selectedFilters, onFilterChange, resetSearch }) => {
    return (
        <div className="sidebar"

             style={{
                 display: 'flex',
                 flexWrap: 'wrap',
                 justifyContent: 'center',
             }}
        >
            {filters.map((filter) => (
                <label className="item" key={filter}>
                    <input
                        type="checkbox"
                        className="checkbox"
                        checked={selectedFilters.includes(filter)}
                        onChange={() => onFilterChange(filter)}
                    />
                    {filter}
                </label>
            ))}
            <button type="button" className={"bg-red-500 hover:bg-red-700 text-white font-bold py-2 px-4 rounded"} onClick={resetSearch}>
                Reset search
            </button>
        </div>
    );
};

export default Sidebar;