import React from 'react';
import Modal from 'react-modal';

interface Props {
    isOpen: boolean;
    closeModal: () => void;
    imageUrl: string;
    onClose: () => void;
}


const modalStyle = {
    position: 'fixed',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    backgroundColor: 'white',
    overflow: 'hidden',
};

const FullScreenImage: React.FC<Props> = ({ isOpen, closeModal, imageUrl , onClose}) => {
    // @ts-ignore
    // @ts-ignore
    return isOpen ? (
        <div
            style={{
                position: 'fixed',
                top: 0,
                left: 0,
                right: 0,
                bottom: 0,
                backgroundColor: 'rgba(0, 0, 0, 0.7)',
                zIndex: 1000,
                cursor: 'zoom-out',
            }}
            onClick={closeModal}
        >
            <div style={{
                position: 'fixed',
                top: '50%',
                left: '50%',
                transform: 'translate(-50%, -50%)',
                backgroundColor: 'white',
                overflow: 'hidden',
            }}>
                <img
                    src={imageUrl}
                    alt=""
                    style={{
                        maxWidth: '80vw',
                        maxHeight: '80vh',
                        width: 'auto',
                        height: 'auto',
                        display: 'block',
                        margin: '0 auto',
                    }}
                    onClick={onClose}
                />
            </div>
        </div>
    ) : null;
};

export default FullScreenImage;